import {detail,joincart} from "../../apis";
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default {
	components:{uniPopup,uniGoodsNav,uniNumberBox},
	data() {
		return {
			size:["m","s",'xs','l','xl','xxl'],
			colorlist:["天蓝色","红珊瑚","白珊瑚","草木绿"],
			datalist:{},
			state:"",
			color:"",
			num:0,
			pd:true,
			id:"",
			token:"",
			options: [{
			          icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/kefu.png',
			          text: '客服'
			        }, {
			          icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/dianpu.png',
			          text: '店铺'
			        }, {
			          icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/carts.png',
			          text: '购物车',
			          info: 0
			        }],
			        buttonGroup: [{
			          text: '加入购物车',
			          backgroundColor: '#ff0000',
			          color: '#fff'
			        },
			        {
			          text: '立即购买',
			          backgroundColor: '#ffa200',
			          color: '#fff'
			        }
			        ]
		}
	},
	onLoad(option) {
		uni.showLoading({
			title: '加载中'
		});
		detail(option.id).then(res=>{
			let [err,data]=res;
			this.datalist=data.data.data
			this.id=this.datalist.basicInfo.id
			uni.hideLoading();
			console.log(this.datalist)
		})
	},
	onShow() {
		try {
		    const value = uni.getStorageSync('token');
		    if (value!=="") {
		        this.token=value.token
		    }else{
				this.token==""
			}
		} catch (e) {
		    // error
		}
	},
	methods: {
		pick1(option){
			this.state=option.item;
		},
		pick2(option){
			this.color=option.item;
		},
		tocart(){
			this.$refs.popup.close()
			if(this.token==""){
				uni.showToast({
					title:"请先登录！",
					icon:"none"
				})
			}else{
				joincart({
					goodsId:this.id,
					number:this.num,
					token:this.token
				}).then(res=>{
					let [err,data]=res;
					console.log(data.data.msg)
				})
				uni.showToast({
					title:"已添加到购物车"
				})
			}
		},
		topay(){
			this.$refs.popup.close()
			if(this.token==""){
				uni.showToast({
					title:"请先登录！",
					icon:"none"
				})
			}else{
				this.serverData = [];
				this.serverData.push({"goodsId":this.datalist.basicInfo.id,"number":this.num,"pic":this.datalist.basicInfo.pic,"name":this.datalist.basicInfo.name,"price":this.datalist.basicInfo.minPrice});
					 this.navData = JSON.stringify(this.serverData)
					 uni.navigateTo({
					 url:"../order/order?index="+this.navData
				}) 
			}
		},
		onClick (e) {
		    uni.showToast({
		    title: `点击${e.content.text}`,
		    icon: 'none',
		})
		 },
		 buttonClick (e) {
		    if(e.index==0){
				 this.$refs.popup.open()
				 this.pd=true
				console.log("加入购物车")
			}else{
				this.$refs.popup.open()
				this.pd=false
				console.log("立即购买")
			}
		},
		bindChange(value){
			this.num=value; 
		}
	  }
	}
